.main__hero {
  position: relative;

  &::after {
    position: absolute;
    z-index: -1;
    content: "";
    left: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    background: $gradient-hero-desktop;

    @include vp-tablet {
      background: $gradient-hero-tablet;
    }

    @include vp-mobile {
      background: $gradient-hero-mobile;
    }
  }
}

.main__footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  @include vp-tablet {
    display: block;
  }
}
